<template>
    <div class="productdisplay">
        <div class="bg">
            <p>店铺</p>
        </div>
        <div class="neirong">
           <div class="fle">
                <!-- <img :src="it.img" alt=""> -->
                <div style="width: 500px;height:400px;">
                    <pic-zoom :url="it.img" :scale="3" ></pic-zoom>
                </div>
                <div class="fexx">
                    <p>{{it.nickname}}￥{{it.money}}</p>
                    <p>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                    </p>
                    <p>{{it.tro}}</p>
                    <p><span>类别：</span>护肤品</p>
                    <p><span>标签：</span>时尚</p>
                    <p>
                        <button @click="num--">-</button>
                        <input v-model="num" disabled="disabled"/>
                        <button @click="num++">+</button>
                        <span  @click="submit">添加到购物车<i class="el-icon-shopping-cart-2"></i></span>
                    </p>
                        <el-tabs v-model="activeName" @tab-click="handleClick" class="tab">
                            <el-tab-pane label="描述" name="first">{{it.describe}}</el-tab-pane>
                            <el-tab-pane label="详情信息" name="second">{{it.details}}</el-tab-pane>
                        </el-tabs>
                </div>
            </div>
        </div>
    </div>
</template> 
<script>
import PicZoom from 'vue-piczoom'
export default {
    data(){
        return{
            it:null,
            num:1,
            activeName: 'first'
        }
    },
    watch:{
             num:function(){
            if(this.num<=0){
                this.num=1
            }
        }
    },
    components:{
        PicZoom
    },
    created(){
        let it=this.$route.params.item;
        this.it=it||JSON.parse(localStorage.getItem("prit"));
        localStorage.setItem("prit",JSON.stringify(this.it));
        console.log(this.it);
    },
    methods:{
          submit(){
            this.it.count=this.num;
            this.num=1;
            this.$emit('chuanzhi',this.it)
        },
           handleClick(tab, event) {
        console.log(tab, event);
      }
    }
}
</script>
<style lang="less" scoped>
.productdisplay{
    margin: 30px 0;
    .foot{
        width: 60%;
        margin: 0 auto
    }
    .bg {
    width: 100%;
    height: 400px;
    position: relative;
    background: url(../assets/home/shopbanner.jpg) no-repeat;
    p {
      font-size: 60px;
      position: absolute;
      left: 300px;
      top: 120px;
    }
  }
  .neirong{
      margin: 0 auto;
      width: 60%;
      .fle{
           display: flex;
           justify-content: space-around;
           .fexx{
               display: flex;
               flex-direction: column;
               width: 50vw;
               font-size: 1vw;
               margin-left: 20px;
                p{
                   text-align: left;
                   font-size: 1vw;
                   margin-top: 30px;
               }
               p:nth-child(1){
                   font-size: 2vw;
                   font-weight: bold;
                   margin:0;
               }
              
               p:nth-child(2){
                   font-size: 1.5vw;
                   margin: 0;
                   i{
                       color:#faa7a3;
                   }
               }
               p:nth-child(4){
                   margin-top: 50px;
                   span{
                       font-weight: bold;
                   }
               }
               p:nth-child(5){
                   margin-top: 10px;
                   span{
                       font-weight: bold;
                   }
               }
               p:nth-child(6){
                   button{
                       width: 47px;
                       height: 47px;
                       background: white;
                       border: none;
                       border: 1px solid black;
                       &:hover{
                           background: #333333;
                           color: white;
                       }
                   }
                   input{
                       vertical-align: bottom;
                       display:inline-block;
                       width: 45px;
                       height: 45px;
                       border: 1px solid black;
                       border-left: none;
                       border-right: none;
                       text-align: center;
                   }
                   span{
                       margin-left: 20px;
                       width: 150px;
                       height: 45px;
                       display: inline-block;
                       border: 1px solid black;
                       vertical-align: bottom;
                       text-align: center;
                       position: relative;
                       line-height: 45px;
                       i{
                           position: absolute;
                           top:10px;
                           left: 160px;
                           color: white;
                            z-index: 10;
                            font-size: 25px;
                       }
                   }
                   span::after{
                       content: "";
                       position: absolute;
                       display: block;
                       width: 47px;
                       height: 47px;
                       background: #999999;
                       top: -1px;
                       left: 150px;
                   }
                   span:hover{
                       background: #000;
                       color: white;
                       cursor: pointer;
                   }
                   
               }
           }
      }
  }
}
</style>
<style>
   .tab{
       font-size: 18px;
       margin-top: 20px;
       margin-bottom: 100px;
   }
   .tab .el-tabs__item{
       color: #888;
       font-size: 18px;
   }
   .tab .el-tabs__item:hover{
       color:#888;
   }
   .tab .el-tabs__item.is-active{
       color: black;
   }
   .tab .el-tabs__active-bar{
       background: #000;
   }
   .tab .el-tab-pane{
       color:#888;
   }
</style>